import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  data:any = [];
  scrolledData = [];
  loading:boolean;
  filterText: string;
  currentIndex = 0;
  scrollHeight = 0;
  cities = ['MUMBAI', 'DELHI', 'BANGALORE', 'CHENNAI', 'VARANASI'];
  city = 'MUMBAI';
  constructor(private http: HttpClient) {
    this.getData();
    window.addEventListener('scroll', this.pushData.bind(this));
  }

  checkScrolled() {
    return window.scrollY - this.scrollHeight > 4000 
  }

  pushData() {
    if(this.scrollHeight && !this.checkScrolled()) {
        return;
    }
    this.scrollHeight = window.scrollY;
    let maxIterate = 100;
    while(maxIterate > 0 && this.currentIndex <= this.data.length) {
        if(this.filterText) {
            for (const key of Object.keys(this.data[this.currentIndex])) {
                if(this.data[this.currentIndex][key].toString().toLowerCase().indexOf(this.filterText) !== -1) {
                    this.scrolledData.push(this.data[this.currentIndex]); 
                    break;
                }
            }
        } else {
            this.scrolledData.push(this.data[this.currentIndex]);
        }
        maxIterate--;
        this.currentIndex++;
    }
  }

  resetData() {
    this.scrolledData = [];
    this.currentIndex = 0;
    this.scrollHeight = 0;
    window.scrollTo(0, 0);
    this.pushData();
  }

  getData() {
    const url = 'https://vast-shore-74260.herokuapp.com/banks?city=' + this.city;
    this.loading = true;
    this.scrolledData = [];
    this.filterText = '';
    this.http.get(url).subscribe((data) => {
        this.loading = false;
        this.data = data;
        this.pushData();
    }, (err) => {
        console.log(err);
    });
  }

  showScroll() {
      return window.scrollY > 400;
  }
  scrollTop() {
    window.scrollTo(0, 0);
  }
}
